body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}




html {
  scrollbar-width: thin; /* Kch thc thanh cun cho Firefox */
  scrollbar-color: #888 transparent; /* Mu thanh cun v nn cho Firefox */
}

html::-webkit-scrollbar {
  width: 12px; /* Chiu rng thanh cun cho Chrome, Safari, Edge */
}

html::-webkit-scrollbar-track {
  background: transparent; /* Mu nn ca track */
}

html::-webkit-scrollbar-thumb {
  background-color: #888888; /* Mu ca thanh cun */
  border-radius: 6px; /* Bo trn gc */
}

html::-webkit-scrollbar-thumb:hover {
  background-color: #555; /* Mu khi hover */
}